import notify from '../components/NotifyBox.vue'
import Vue from "vue";

const notifyExtended = Vue.extend(notify);

const Notify = function(options = {}) {
    if(typeof options ==='string') {
        options = {
            msg: options
        }
    }
    var notifyObj = new notifyExtended({
        data() {
            return {
                visiable :true,
                msg: options.msg,
            }
        }
    }).$mount('#notify')


    setTimeout(()=>{
        notifyObj.visiable =false;
    },3000)

}

export default Notify