import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
      {
       path: '/overview',
       name: 'Overview',
       component: () => import('../views/Overview.vue')
      },
      {
        path: '/create',
        name: 'CreatePosts',
        component: () => import('../views/CreatePosts.vue')
      },
      {
        path: '/upload',
        name: 'UploadPosts',
        component: () => import('../views/UploadPosts.vue')
      },
      {
        path: '/adminposts',
        name: 'AdminPosts',
        component: () => import('../views/AdminPosts.vue')
      },
      {
        path: '/pwdmod',
        name: 'PwdMod',
        component: () => import('../views/PwdMod.vue')
      },
      {
        path: '/logout',
        name: 'Logout',
        component: () => import('../views/Logout.vue')
      },
      {
        path: '/editpost/:pid',
        name: 'EditPost',
        component: () => import('../views/CreatePosts.vue')
      },
      {
        path: '/delpost/:pid',
        name: 'DelPost',
      },
      {
        path: '/',
        name: 'Login',
        component: () => import('../views/Login.vue')
      },
      {
        path: '/sitesettings',
        name: 'SiteSettings',
        component: ()=> import('../views/SiteSettings.vue')
      },
      {
        path: 'databasebackup',
        name: 'DatabaseBackup',
        component: () => import('../views/DatabaseBackup.vue')
      },
      {
        path: 'commentsmgmt',
        name: 'CommentsMgmt',
        component: () => import('../views/CommentsMgmt.vue')
      },
      {
        path: 'menubar',
        name: 'Menubar',
        component: () => import('../views/Menubar.vue')
      }
  ]

const router = new VueRouter({
  mode: 'hash',
  //base: process.env.BASE_URL,
  base: '/mgmt/',
  routes
})

router.beforeEach((to, from, next) => {
  if (to.name !== 'Login' && !window.sessionStorage.token){
    next({path: '/'});
  } else {
    next();
  }
})

export default router
